import React, { Suspense, lazy } from "react";
import style from "./Stats.module.css";
import { useLocation } from "react-router-dom";
import QvipleLoader from "../../../../optimized/Loader/QvipleLoader";
// import GiveStudentInternalEvaluation from "../../../../optimized/UserStudentMember/GiveStudentInternalEvaluation/GiveStudentInternalEvaluation";

const Notification = lazy(() => import("./Notification/Notification"));
const Announcement = lazy(() => import("./Announcement/Announcement"));
const Activity = lazy(() => import("../Activity/Activity"));
const StudentMessage = lazy(() => import("../StudentMessage/StudentMessage"));
const StudentMessageActivityDetail = lazy(() =>
  import("../StudentMessage/StudentMessageActivityDetail")
);
const GiveStudentFeedback = lazy(() =>
  import(
    "../../../../optimized/UserStudentMember/GiveStudentFeedback/GiveStudentFeedback"
  )
);

function Stats() {
  const getQuery = useLocation();

  return (
    <div className={style.stats}>
      <Suspense fallback={<QvipleLoader />}>
        {!getQuery.search && <Notification />}
        {getQuery.search?.substring(3, 15) === "announcement" && (
          <Announcement />
        )}
        {getQuery.search?.substring(3) === "activity" && <Activity />}
        {getQuery.search?.substring(3) === "student-messaage" && (
          <StudentMessage />
        )}
        {getQuery.search?.substring(3) === "student-messaage-detail" && (
          <StudentMessageActivityDetail />
        )}
        {getQuery.search?.substring(3) === "feedback" && (
          <GiveStudentFeedback />
        )}
        {/* {getQuery.search?.substring(3) === "internal-evaluation" && (
          <GiveStudentInternalEvaluation />
        )} */}
      </Suspense>

      {/* {clickItem === 3 && <Announcement clickItem={clickItem}/>}
      {clickItem === 4 && <Announcement clickItem={clickItem}/>} */}
    </div>
  );
}

export default Stats;
